/******************************************************************* 
Basic App Styling SASS Variables
********************************************************************/

$base-background-color: #1E1E1E;
$base-background-grey: #1b1b1b; 
$base-font-color: white;
$base-hover-color: #BD4517;
$secondary-font-color: #8d8d8d;


/******************************************************************* 
Layout Styling
********************************************************************/
html {
  background-color: $base-background-color;
}

a {
  text-decoration: none;
}

body {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  -webkit-font-smoothing: antialiased;
  overflow-y: scroll;
  overflow-x: hidden;
}

/******************************************************************* 
Basic Page Containers
********************************************************************/

.page-container-fixed {
  display: flex;
  justify-content: flex-start;
  margin: 0 40px 0 100px;
  position: fixed;
  width: 100%;
  min-height: 100vh;
}

@media only screen and (max-width: 900px) {
  .page-container-fixed {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    position: unset;
    width: 100%;
    height: 100%;
    margin: 0;
  }
}

.page-container-scroll {
  display: flex;
  justify-content: flex-start;
  margin: 0 40px 0 100px;
  width: 100%;
  height: 100vh;
}

@media only screen and (max-width: 900px) {
  .page-container-scroll {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    width: 100%;
    height: 100%;
    margin: 0;
  }
}


/******************************************************************* 
Navbar Styling
********************************************************************/

.navBar {
  display: flex;
  color: $base-font-color;
  top: 0;
  left: 0;
  height: 100%;
  width: 100px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  min-height: 500px;
  background-color: #191919;
  z-index: 100;
  overflow-y: scroll;
}

@media only screen and (max-width: 900px) {
  .navBar {
    background-color: $base-background-color;
    display: none;
  }
}


h1.myName {
  font-size: 2rem;
  line-height: 1;
  color: $base-hover-color;
  margin-top: 50px;
}

ul.menu > li:hover {
  color: $base-hover-color;
  cursor: pointer;
}

.menu {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding-inline-start: 0;
  font-size: 17px;
  letter-spacing: 2px;
  width: 100%;
  text-align: center;
}

.menu > li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  fill: $base-hover-color;
  
}

.menu > li:hover {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  fill: $base-hover-color;
  text-decoration: none;
}

.menu > a > li.home:hover::after {
  visibility: visible;
  content: 'HOME';
  font-size: 11px;
  display: flex;
  justify-content: center;
  color: $base-hover-color;
  text-decoration: none;
  
}

.menu > a > li.about:hover::after {
  visibility: visible;
  content: 'ABOUT';
  font-size: 11px;
  display: flex;
  justify-content: center;
  color: $base-hover-color;
  text-decoration: none;
}

.menu > a > li.skills:hover::after {
  visibility: visible;
  content: 'SKILLS';
  font-size: 11px;
  display: flex;
  justify-content: center;
  color: $base-hover-color;
  text-decoration: none;
}

.menu > a > li.work:hover::after {
  visibility: visible;
  content: 'WORK';
  font-size: 11px;
  display: flex;
  justify-content: center;
  color: $base-hover-color;
  text-decoration: none;
}

.menu > a > li.contact:hover::after {
  visibility: visible;
  content: 'CONTACT';
  font-size: 11px;
  display: flex;
  justify-content: center;
  color: $base-hover-color;
  animation: fadeIn 1s 1;
  text-decoration: none;

  @keyframes fadeIn {
    from { opacity: 0;}
    to { opacity: 1;}
  }
}

.home:hover .home-icon {
  display: none;
}

.about:hover .about-icon {
  display: none;
}

.skills:hover .tech-icon {
  display: none;
}

.tech:hover .tech-icon {
  display: none;
}

.work:hover .work-icon {
  display: none;
}

.contact:hover .contact-icon {
  width: 100px;
  position: absolute;
  animation: sentOut 0.5s 1;
  animation-timing-function: ease-out;

  @keyframes sentOut {
    from {left: 0px; opacity: 1;}
    to{left: 100px; opacity: 0;}
  }
  opacity: 0;
}

.social-media {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding-inline-start: 0;
  width: 100%;
}

.social-media > li {
  display: flex;
  padding: 0 10px;
  flex-direction: column;
  justify-content: center;
  font-size: 11px;
  letter-spacing: 2px;
  text-align: center;
  height: 40px;
  align-items: center;
}

.social-media > li > a {
  display: flex;
  color: white;
  text-decoration: none;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-size: 17px;
}

.social-media > li > a:hover {
  color: $base-hover-color;
  cursor: pointer;
}

@media only screen and (max-width: 900px) {
  .social-media > li > a:hover {
    color: $base-hover-color;
    cursor: pointer;
  }
}

.navBar > a {
  color: $base-hover-color;
  text-decoration: none;
}

.navBar > li > a {
  color: $base-hover-color;
  text-decoration: none;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

ul.menu > a > li {
  color: white;
  text-decoration: none;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul.menu > a:active {
  text-decoration: none;
}

 ion-icon {
   color: white;
 }


