/******************************************************************* 
Skill Card
********************************************************************/

li.skill-card {
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
  margin: 0 0 40px 0;
  padding: 10px 0;
  justify-content: space-around;
  border: solid $secondary-font-color 1px;
  background-color: $base-background-grey;
  border-radius: 10px;
  padding: 10px;
  width: 85%;
  align-self: center;
}

img.skill-image {
  display: flex;
  height: 50px;
  font-size: 12px;
  align-items: center;
}

img.inverseWhite {
  filter: invert(1);
}

ul.skill-cards {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/******************************************************************* 
Skill Card Animations
********************************************************************/

.skill-card-enter {
  opacity: 0.01;
  transition: 1s;
  transform: translateX(220%);

  &.skill-card-enter-active {
    transition: 1s;
    transform: translateX(0%);
    opacity: 1;
   }
}

.skill-card-exit {
  opacity: 1;
  transition: 0.5s;
  transform: translateX(0);
  &.skill-card-exit-active {
    transition: 0.5s;
    transform: translateX(220%);
    opacity: 0.01;
  }
}

@media only screen and (max-width: 900px) {
  .skill-card-enter {
    opacity: 0.01;
    transition: 1s;
    transform: unset;
    &.skill-card-enter-active {
      transition: 1s;
      opacity: 1;
      transform: unset;
     }
  }

  .skill-card-exit {
    opacity: 1;
    transition: 0.5s;
    transform: unset;
    &.skill-card-exit-active {
      transition: 0.5s;
      opacity: 0.01;
      transform: unset;
    }
  }
}

